@import "base/_vars";

@import "modules/_buttons",
        "modules/_controls",
        "modules/board";

html, body {
  width: 100%;
  height: 100%;
  
  margin: 0;
  padding: 0;
  font-family: sans-serif;

  background-color: darken($base-color, 25);
}

 h1 {
    margin-top: 0px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    color: $font-color;
 }

 #root {
   padding: 30px;
 }

 .game-container {
   position: absolute;
   left: 50%;
   -webkit-transform: translateX(-50%);
       -ms-transform: translateX(-50%);
           transform: translateX(-50%);

   display: -ms-flexbox;

   display: flex;
   -ms-flex-align: center;
       align-items: center;
   -ms-flex-direction: column;
       flex-direction: column;
   padding: 30px;
   border: 5px solid $life-orange-color;
   border-radius: 10px;

   background-color: $color-grey-blue;
   -webkit-box-shadow: 4px 4px 10px 3px rgba(0,0,0,0.3);
           box-shadow: 4px 4px 10px 3px rgba(0,0,0,0.3);
 }

 .life-red {
   color: $life-red-color;
 }

 .life-orange {
   color: $life-orange-color;
 }

 .footer {
   position: absolute;
   bottom: -100px;

   width: 100%;

   color: $font-color;
   text-align: center;
   font-size: 2em;
   letter-spacing: 0.25em;

   & a {
     color: $font-color;
   }

   & a:hover {
     color: darken($font-color, 20);
   }
 }



